@charset "UTF-8";
blockquote {
  margin: 0;
  padding: 0;
  position: relative;
  font-size: 0.25vw;
  line-height: 1.4;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-fon-smoothing: grayscale;
}

blockquote p:first-of-type:before {
  content: "“";
  position: absolute;
  left: -0.5em;
}

blockquote p:last-of-type:after {
  content: "”";
  position: absolute;
}

blockquote footer {
  position: relative;
}

blockquote footer:before {
  content: "–";
  position: absolute;
  left: -0.6em;
}

@media screen and (max-width: 1600px) {
  blockquote {
    font-size: 1.5vw;
  }
}
@media all and (max-width: 1200px) {
  blockquote {
    font-size: calc(12px + 0.5vw);
  }
}
@media all and (max-width: 600px) {
  blockquote {
    font-size: 16px;
  }
}/*# sourceMappingURL=quoteapi.css.map */