.quote {
    grid-area: quote;
    width: 30vw;
    height: 50vh;

    display: flex;
    align-items: center;
    justify-content: space-around;
    font-family: 'EB Garamond', serif;
    background-color: #fff;
    padding: 0 2em;
}
@media only screen and (max-width: 600px) {
    .quote {
        width: 100vw;
        height: 15vh;
    }
}