.youtube {
  grid-area: youtube;
  width: 30vw;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.youtube iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 600px) {
  .youtube {
    width: 100vw;
    height: 27vh;
  }
}/*# sourceMappingURL=youtube.css.map */