.newtask{
    position: relative;
    margin-top: 20px;
}
.input{
    background: #F2F2F2;
    border: 1px solid #07C7A4;
    border-radius: 7px;
    height: 50px;
    width: 100%;

    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 34px;
    
    padding: 5px 10px;
    color: #333333;
}
.button{
    position: absolute;
    top: 0;
    right: 0;

    background: #07C7A4;
    border-radius: 7px;
    height: 50px;
    width: 174px;

    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 34px;
        
    text-align: center;
    color: #FFFFFF;
}
@media only screen and (max-width: 600px) {
    .button {
        width: 100%;
        top: 52px;
    }
}