.label{
    display:block;

}    
.checkbox {
    position:relative;
    display:block;
    width:100%;
    height:60px;
    border-bottom: 1px solid black;
    overflow:hidden;

}
.checkbox:not(:last-child){
    border-bottom:2px solid white;
}
.check {
    width:50px;
    height:50px;
    position:absolute;
    opacity:0;
}
.label{
    cursor: pointer;
    margin-top: 9px;
}
.label span {
    position:absolute;
    margin-top:13px;
}
.path1{
    stroke-dasharray:400;
    stroke-dashoffset:400;
    transition:.5s all;
}
.check:checked + label svg g path {
    stroke-dashoffset:0;
}
.edit{
    position: absolute;
    top: 25px;
    right: 40px;
    cursor: pointer;
}
.delete{
    position: absolute;
    top: 25px;
    right: 10px;
    cursor: pointer;
}